"use strict"
import { Component, OnInit, Inject } from '@angular/core'
import { Router } from '@angular/router'
import { AppComponent } from '../app.component'

@Component({
  selector: 'experience',
  templateUrl: '../templates/xperience.html'
})

export class XperienceComponent implements OnInit{
	public jobs:Array<{name,role,projects,from,to}> = [
		{
			name:'Almacenes Éxito',
			role:'Analista II - Tecnología',
			projects:[
				{name:'www.exito.com'},
				{name:'www.carulla.com'},
				{name:'Nuevos aplicativos'}
			],
			from:'Ago, 2017',
			to:'Actually'
		},
		{
			name:'Sophos Banking Solutions',
			role:'Implementation Consultant',
			projects:[
				{name:'CDT'},
				{name:'Indebtness'}
			],
			from:'Mar, 2016',
			to:'Ago, 2017'
		},
		{
			name:'Mercash Digital S.A.S.',
			role:'Front-end Web Developer',
			projects:[
				{name:'Tipon'}
			],
			from:'Jul, 2014', 
			to:'Mar, 2016'
		}
	]

	constructor(private _router:Router, @Inject(AppComponent) private parent: AppComponent){}

	ngOnInit(){
		this.parent.choose('experience')
	}
}



