"use strict"
import { Component, OnInit, Inject } from '@angular/core'
import { Router } from '@angular/router'
import { AppComponent } from '../app.component'

@Component({
  selector: 'contact',
  templateUrl: '../templates/contact.html'
})

export class ContactComponent implements OnInit{
	public name:string = "Neiro Andres Torres Castro"
	public email:string = "neiroandres@yahoo.com.co"
	public mobile:string = "(+57) 3053671792"
	public location:string = "Medellín, Colombia"
	
	public emailTitle:string = "Email:"
	public mobileTitle:string = "Móvil:"
	public locationTitle:string = "Ubicación:"

	public cvTitle:string = "Solicita mi Currículo!:"
	public cv:Array<{lang,link}> = [{
		lang:"Español",
		link:"https://drive.google.com/open?id=0B1uPRn_EOqfDekNpOUhtQmdBUzg"
	},
	{
		lang:"English",
		link:"https://drive.google.com/open?id=0B1uPRn_EOqfDQ25wWkljaVpBazg"
	}]

	constructor(private _router:Router, @Inject(AppComponent) private parent: AppComponent){}

	ngOnInit(){
		this.parent.choose('contact')
	}
}



