"use strict"
import { Component, OnInit, Inject } from '@angular/core'
import { Router } from '@angular/router'
import { AppComponent } from '../app.component'

@Component({
  selector: 'about',
  templateUrl: '../templates/about.html'
})

export class AboutComponent implements OnInit{
	public name:string="Portafolio Neiro Torres"
	public title:string = "Framework: "
	public framework:string = "Angular"
	
	public addTools:Array<{tool,version}> = [{
			tool:"Angular CLI: ",
			version:"4.0.0"
		},
		{
			tool:"Bootstrap: ",
			version:"4"
		},
		{
			tool:"NodeJS: ",
			version:"6.0.60"
		},
		{
			tool:"Typescript: ",
			version:"2.2.0"
		}]

	constructor(private _router:Router, @Inject(AppComponent) private parent: AppComponent){}

	ngOnInit(){
		this.parent.choose('about')
	}
}


